import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import App from './views/ToyApp.vue'
import Details from './views/ToyDetails.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/app',
      name: 'app',
      component: App
    },
    {
      path: '/app/:toyId',
      name: 'details',
      component: Details
    }
  ]
})
