import store from '../store.js'
const axios = require('axios')
const URL = (process.env.NODE_ENV !== 'development')
            ? '/toys'
            : '//localhost:3000/toys';




function addToy({newToy}) {
    console.log('toyService: ', newToy)
    return axios.post(URL, newToy)
}

function loadToys() {
    return axios.get(URL)
        .then(res => {
            console.log('toyService loaded toys:', res.data)
            return res.data})
}

function getToyById({toyId}) {
    return axios.get(`${URL}/${toyId}`) 
}

function removeToy(toyId) {
    return axios.delete(`${URL}/${toyId}`)
}

function updateToy(toy) {
    console.log(toy)
    return axios.put(`${URL}/${toy.id}`, toy)
        .then(res => res.data)
}

export default {
    addToy,
    loadToys,
    removeToy,
    updateToy,
    getToyById
}