import Vue from 'vue'
import Vuex from 'vuex'
import toyService from '../service/toyService.js'

export default {
    strict: true,
    state: {
        toys: []
    },
    mutations: {
        addToy(state, { newToy }) {
            state.toys.unshift(newToy)
        },
        removeToy(state, { toyId }) {
            state.toys = state.toys.filter(toy => toy._id !== toyId)
        },
        setFilter(state, payload) {
            state.filterBy = payload.filterBy
        },
        loadToys(state, payload) {
            state.toys = payload.toys
        },
        updateToy(state, payload) {

        }
    },
    actions: {
        addToy(context, { newToy }) {
            return toyService.addToy({ newToy })
                .then((res) => {
                    return context.commit({ type: 'addToy', newToy: res.data })
                })
        },
        loadToys(context) {
            return toyService.loadToys()
                .then(res => { context.commit({ type: 'loadToys', toys: res }) })
        },
        getToyById(context, { toyId }) {
            return toyService.getToyById(toyId)
        },
        removeToy(context, { toyId }) {
            return toyService.removeToy(toyId)
                .then(() => { context.commit({ type: 'removeToy', toyId }) })
        },
        updateToy(context, { newToy }) {
            console.log('new toyyy to update',newToy)
            return toyService.updateToy( newToy )
                // .then((res) => {
                //     return context.commit({ type: 'updateToy', newToy: res.data })
                // })
        },
    },
    getters: {
        toysToShow(state) {
            return state.toys
        }
    }
}
