<template>
  <div class="toy-list">
      <button v-if="!isAddToy && !paramsId" @click="isAddToy = true">New toy</button>
      
      <section v-if="isAddToy || paramsId" class="add-toy">
          <form @submit.prevent="addToy">
            Name: <input type="text" v-model="toyToEdit.name">
            Price: <input type="number" v-model="toyToEdit.price">
            Type: <select v-model="toyToEdit.type">
                    <option value="Kids">Kids</option>
                    <option value="Adult">Adult</option>    
                    </select>        
            <button type="submit">Add!</button>
          </form>
      </section>
  </div>
</template>

<script>
export default {
  props: ['toy'],
  name: "ToyEdit",
  data() {
    return {
      toyToAdd: {
        name: "",
        price: null,
        type: "",
        inStock: true,
        createdAt: Date.now()
      },
      isAddToy: false,
    };
  },
  mounted() {

  },
  computed: {
    paramsId() {
      return this.$route.params.toyId;
    },
    toyToEdit() {
      if (this.paramsId) return this.toy
      else return this.toyToAdd
    }

  },
  methods: {
    addToy() {
      let newToy = this.toyToEdit;
      if (this.isAddToy) {
      this.$store.dispatch({ type: "addToy", newToy });
      this.isAddToy = false;
      } else {
          this.$store.dispatch({ type: "updateToy", newToy })
            .then(() => this.$router.push('/app'))
      }
    }
  }
};
</script>

