<template>
  <div class="toy-details">
              <h1>Name: {{toy.name}}</h1>
          <section class="toy-info">
              <h2>Price: ${{toy.price}} |</h2>
              <hr>
              <h2>Type: {{toy.type}} |</h2>
              <hr>              
              <h2 v-if="toy.inStock">In stock!</h2>    
              <h2 v-else>Out of stock :(</h2>          
          </section>
              <button @click.prevent="removeToy(toy._id)">Delete</button> 
              <section class="edit-box">
               <h2>Edit toy:</h2> 
              <ToyEdit :toy="toy"></ToyEdit>
              </section>
  </div>
</template>

<script>
import ToyEdit from "@/components/ToyEdit.vue";

export default {
  name: "ToyDetails",
  data() {
    return {
      toy: {},
    };
  },
  methods: {
    removeToy(toyId) {
      this.$store.dispatch({ type: "removeToy", toyId }).then(() => {
          console.log("Toy is deleted");
        this.$router.push("/app");
      });
    }
  },
  components: {
    ToyEdit
  },
  created() {
    let toyId = this.$route.params;
    this.$store.dispatch({ type: "getToyById", toyId })
            .then(res => (this.toy = res.data));
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.toy-details {
  padding: 20px;
  border: 3px solid green;
  border-radius: 5px;
}
.toy-info {
  padding: 20px;
  border: 3px solid black;
  border-radius: 5px;
}
</style>
